.GameInit {
	text-align: center;
	display: grid;
	grid-template-columns: 1fr 3fr 5fr 1fr;
	grid-template-rows: 11;
}

.shipContainer {
	grid-row-start: 2;
	grid-column-start: 2;
	grid-column-end: 3;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	font-size: 1.3em;
}

.InitBoardContainer {
	grid-column-start: 3;
	justify-self: center;
}

.startButton {
	grid-column-start: 2;
	grid-row-start: 2;
}

.initDisplay {
	height: 80px;
	grid-column-start: 2;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 2;
	display: flex;
	flex-direction: column;
	font-size: 2em;
}
.readyWrapper {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: flex-end;
	/* padding-bottom: 10px; */
	flex-grow: 1;
}

.gameTitle {
	grid-column-start: 3;
	grid-column-end: 4;
	display: flex;
	justify-self: center;
}

